.slick-arrow {
  position: absolute;
  display: block;
  font-size: 0;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  transition: all .3s; }

.slick-prev:after {
  border: 5px solid transparent;
  content: "";
  display: block;
  position: absolute;
  height: 0;
  width: 0;
  border-right-width: 8px;
  border-left-width: 0;
  top: 50%;
  right: 22px;
  transform: translateY(-50%);
  transition: all .3s; }

.slick-next:after {
  border: 5px solid transparent;
  content: "";
  display: block;
  position: absolute;
  height: 0;
  width: 0;
  border-left-width: 8px;
  border-right-width: 0;
  top: 50%;
  left: 22px;
  transform: translateY(-50%);
